@use "sass:color";

@import "shared";

.News {
  .news-card {
    ul, ol, p {
      margin: .5em 0;
    }
    .card-title {
      margin-bottom: .75em;
      font-size: 140%;
      color: $primaryColor;
      font-weight: 500;
    }
    .details,
    .date {
      font-size: 85%;
      // font-weight: bold;
      opacity: .5;
    }
    .details {
      margin: 1.25em 0;
    }
    .card-body {
      padding: 1em 1.5em;
    }
  }
}
